<header class="header pt-4 pb-4"><div class="container d-flex justify-content-between align-items-center"><div class="menu ms-auto me-auto"><input id="checkbox" type="checkbox" @click.stop="activeMenu"><label class="toggle" for="checkbox"> <div class="bars" id="bar1"></div><div class="bars" id="bar2"></div><div class="bars" id="bar3"></div></label></div><ul class="ps-0 mb-0 links justify-content-between align-items-center"><li v-for="route in $router.getRoutes()"><router-link class="text-capitalize text-decoration-none" v-if="route.name !== 'contact us'" :to="route.name" v-text="route.name"></router-link><router-link class="d-block button text-decoration-none text-capitalize text-center" v-else :to="{ name: 'contact us' }" v-text="route.name"></router-link></li></ul></div></header>